document.addEventListener('DOMContentLoaded', function() {
	startApp();
	
	// Instancia del Typed.js para el texto animado
	
	const typed = new Typed('.typed', {
		// strings: ['<i class="class">Online</i>', '<i class="class">Segura</i>', '<i class="class">Confiable</i>'],
		stringsElement: '#cadenas-texto', // ID del elemento que contiene cadenas de texto a mostrar.
		typeSpeed: 100, // Velocidad en mlisegundos para poner una letra,
		startDelay: 300, // Tiempo de retraso en iniciar la animacion. Aplica tambien cuando termina y vuelve a iniciar,
		backSpeed: 80, // Velocidad en milisegundos para borrrar una letra,
		smartBackspace: true, // Eliminar solamente las palabras que sean nuevas en una cadena de texto.
		shuffle: false, // Alterar el orden en el que escribe las palabras.
		backDelay: 1250, // Tiempo de espera despues de que termina de escribir una palabra.
		loop: true, // Repetir el array de strings
		loopCount: false, // Cantidad de veces a repetir el array.  false = infinite
		showCursor: true, // Mostrar cursor palpitanto
		cursorChar: '|', // Caracter para el cursor
		contentType: 'html', // 'html' o 'null' para texto sin formato
	});
});

function startApp() {
	addDate();
	fixedNav();
	scrollNav();
	menuBtn();
}

function addDate() {
	const systemDate = new Date();
	const year = systemDate.getFullYear();
	const footer = document.querySelector('.footer');

	const copyright = document.createElement('P');
	copyright.innerHTML = `Secretary Online, Todos los derechos reservados <span>${year}</span> <i class="fa-solid fa-copyright"></i>`;
	copyright.classList.add('copyright');

	footer.appendChild(copyright);
}

function fixedNav() {
	const body = document.querySelector('body');
	const header = document.querySelector('.header');
	const about = document.querySelector('.about');

	window.addEventListener('scroll', function() {
		if( about.getBoundingClientRect().top < 0) {
			header.classList.add('fixed');
			body.classList.add('body-scroll');
		} else {
			header.classList.remove('fixed');
			body.classList.remove('body-scroll');
		}
	});
}

function scrollNav() {
	const links = document.querySelectorAll('.header .nav .nav-link');
	const mainLinks = document.querySelectorAll('.main-link');
	links.forEach( link => {
		link.addEventListener('click', function(e) {
			e.preventDefault();
			
			const sectionScroll = e.target.attributes.href.value;
			const section = document.querySelector(sectionScroll);
			section.scrollIntoView({ behavior: "smooth"});
		});
	});
	mainLinks.forEach( mainLink => {
		mainLink.addEventListener('click', function(e) {
			e.preventDefault();
			const sectionScroll = document.querySelector('#about');
			sectionScroll.scrollIntoView({ behavior: "smooth"});
		});
	});
}

function menuBtn() {
	const menu = document.querySelector('.burgerBtn');
	const mobileMenu = document.querySelector('.mobile-nav');
	const mobileLinks = document.querySelectorAll('.mobile-nav .nav-link')
	menu.addEventListener('click', function() {
		menu.classList.toggle('is-active');
		mobileMenu.classList.toggle('is-active');
	})

	mobileLinks.forEach( mobileLink => {
		mobileLink.addEventListener('click', function() {
			menu.classList.remove('is-active');
			mobileMenu.classList.remove('is-active');
		})
	})
}
